<template>
  <div class="w-full h-screen flex justify-center items-center">
    <div v-if="loading">
      <BaseLoading />
    </div>
    <div v-show="!loading">
      <VuePdf
        :pdf="pdfPreview"
        title
        file-name="shipping-kit"
        page-scale="page-width"
        :config="{
          sidebar:{
            viewThumbnail: true,
            viewOutline: true
          },
          toolbar: {
             toolbarViewerRight: {
              presentationMode: false,
              openFile: false,
              print: false,
              download: true,
              viewBookmark: false,
            },
          },
          secondaryToolbar: false
        }"
        theme="dark"
        style="position: fixed; top: 0; left: 0; width: 100vw; height: 100vh"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
import { usePrint } from 'composable/whitelabel'

import BaseLoading from 'components/BaseLoading.vue'
import VuePdf from 'vue3-pdf-app'

export default defineComponent({
  name: 'ShippingKit',
  components: {
    VuePdf,
    BaseLoading
  },
  setup() {
    const route = useRoute()
    const { printKit, loading } = usePrint()
    const pdfPreview = ref(null)

    onMounted(async () => {
      const queries = Object.keys(route.query)
      const checked = queries.map(item => !!route.query[item]).filter(item => item)
      if ((queries.length !== checked.length) || !queries.length) {
        return window.open(location.origin, '_self')
      }

      return printKit(route.query, (pdfFile) => {
        pdfPreview.value = pdfFile
      })
    })

    return {
      loading,
      pdfPreview
    }
  }
})
</script>
